import { Module } from 'vuex';
import { SiteState, RootState, ISiteItemOrigin } from '@/store/types';
import axios from 'axios';
import { ISiteItem, IListItem } from '@/types/interface';

const site: Module<SiteState, RootState> = {
  namespaced: true,
  state: {
    siteList: [],
  },
  mutations: {
    SAVE_SITE_LIST(state, list) {
      state.siteList = list.map((d: ISiteItemOrigin) => ({
        id: d.id_unit,
        name: d.name_unit,
        areaId: d.id_area,
        areaName: d.name_area,
        tel: d.cram_tel,
        address: d.cram_address,
        url: d.cram_url,
        line: d.Line_Link,
        fb: d.Fb_Link,
      }));
    },
  },
  actions: {
    getSiteList({ commit }) {
      const temp = {
        // eslint-disable-next-line
        id_site: '0001',
        // eslint-disable-next-line
        id_org: '4010',
      };
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/ReadFullData_Site_Management', temp)
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') commit('SAVE_SITE_LIST', data.Data);
            resolve(data.Data);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
  },
  getters: {
    areaList: (state) => {
      const list: IListItem[] = [];
      state.siteList.forEach((item: ISiteItem) => {
        const temp = list.find((s) => s.id === item.areaId);
        if (!temp) {
          list.push({
            id: item.areaId,
            title: item.areaName,
          });
        }
      });
      return list;
    },
    siteByAreaId: (state) => (id: string): ISiteItem[] => state.siteList.filter((d: ISiteItem) => d.areaId === id),
  },
};

export default site;
