import axios from 'axios';
import moment from 'moment';
import { Module } from 'vuex';
import {
  RootState, KindState, ITypeOrigin, IKindOrigin, IPageOrigin, IContentOrigin, IListOrigin,
} from './types';

const pastTest: Module<KindState, RootState> = {
  namespaced: true,
  state: {
    typeMenu: [],
    typeMenuHot: [],
    kindMenu: [],
    typePage: [],
    typeContent: [],
    countryList: [],
    areaList: [],
    acceptList: [],

  },
  mutations: {
    SAVE_TYPE_MENU(state, types) {
      state.typeMenu = types.map((d: ITypeOrigin) => ({
        typename: d.Typename,
        title: d.Title,
      }));
    },
    SAVE_TYPE_MENU_HOT(state, types) {
      state.typeMenuHot = types.map((d: ITypeOrigin) => ({
        typename: d.Typename,
        title: d.Title,
      }));
    },
    SAVE_KIND_MENU(state, types) {
      state.kindMenu = types.map((d: IKindOrigin) => ({
        kindname: d.Kindname,
        title: d.Title,
      }));
    },
    SAVE_TYPE_PAGE_LIST(state, pages) {
      state.typePage = pages.map((d: IPageOrigin) => ({
        pagename: d.Pagename,
        title: d.Title,
      }));
    },
    SAVE_KIND_PAGE_LIST(state, pages) {
      state.typePage = pages.map((d: IPageOrigin) => ({
        pagename: d.Pagename,
        title: d.Title,
      }));
    },
    SAVE_TYPE_PAGE_CONTENT(state, contents) {
      state.typeContent = contents.map((d: IContentOrigin) => ({
        testType: d.TestType,
        title: d.Title,
        note: d.note,
        actId: d.Act_id,
        pageType: d.PageType,
      }));
    },
    SAVE_KIND_PAGE_CONTENT(state, contents) {
      state.typeContent = contents.map((d: IContentOrigin) => ({
        testType: d.TestType,
        title: d.Title,
        note: d.note,
        actId: d.Act_id,
        pageType: d.PageType,
      }));
    },
    SAVE_COUNTRY_LIST(state, list) {
      state.countryList = list.map((d: IListOrigin) => ({
        id: d.Value,
        title: d.Text,
      }));
    },
    SAVE_AREA_LIST(state, list) {
      state.areaList = list.map((d: IListOrigin) => ({
        id: d.Value,
        title: d.Text,
      }));
    },
    SAVE_ACCEPTOR(state, list) {
      state.acceptList = list.map((d: IListOrigin) => ({
        id: d.Value,
        title: d.Text,
      }));
    },
    RESET_INFORMATION(state) {
      state.kindMenu = [];
      state.typePage = [];
      state.typeContent = [];
    },
  },
  actions: {
    getTypeMenu({ commit }) {
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/TestKind/Type_Menu', {
            publicname: 'jobs',
          })
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') commit('SAVE_TYPE_MENU', data.Data);
            resolve();
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    getTypeMenuHot({ commit }) {
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/TestKind/Type_Menu_Hot', {
            publicname: 'jobs',
          })
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') commit('SAVE_TYPE_MENU_HOT', data.Data);
            resolve();
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    getKindMenu({ commit }, params) {
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/TestKind/Kind_Menu', params)
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') commit('SAVE_KIND_MENU', data.Data);
            resolve();
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    getTypePageList({ commit }, type) {
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/TestKind/Type_Page_List', {
            typename: type,
          })
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') commit('SAVE_TYPE_PAGE_LIST', data.Data);
            resolve(data.Data);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    getKindPageList({ commit }, params) {
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/TestKind/Kind_Page_List', params)
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') commit('SAVE_KIND_PAGE_LIST', data.Data);
            resolve();
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    getTypePage({ commit }, params) {
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/TestKind/Type_Page', params)
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') commit('SAVE_TYPE_PAGE_CONTENT', data.Data);
            resolve(data.Data);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    getKindPage({ commit }, params) {
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/TestKind/Kind_Page', params)
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') commit('SAVE_KIND_PAGE_CONTENT', data.Data);
            resolve();
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    getAcceptor({ commit }) {
      const temp = {
        Value: '1597',
      };

      return new Promise((resolve, reject) => {
        axios
          .post('RaspApp/SingleBanked/ReferToAcceptor', temp)
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') commit('SAVE_ACCEPTOR', data.Data);
            resolve();
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    getCounty({ commit }) {
      return new Promise((resolve, reject) => {
        axios
          .post('RaspApp/SingleBanked/GetCounty')
          .then((response) => {
            const { data } = response;
            commit('SAVE_COUNTRY_LIST', data.Data);
            // if (data.Code === '0000') commit('SAVE_KIND_PAGE_CONTENT', data.Data);
            resolve();
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    getArea({ commit }, id) {
      const temp = {
        Value: id,
      };

      return new Promise((resolve, reject) => {
        axios
          .post('RaspApp/SingleBanked/GetArea', temp)
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') commit('SAVE_AREA_LIST', data.Data);
            resolve();
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    saveMessage({ commit }, params) {
      const temp = {
        // eslint-disable-next-line
        act_id: '1597',
        Name: params.Name,
        Tel: params.Mobile,
        Mobile: params.Mobile,
        Email: params.Email,
        postcode: params.postcode,
        Address1: params.city,
        Address2: params.region,
        Dwell1: params.city,
        Dwell2: params.region,
        Classnine: params.class,
        Contact: params.consult,
      };

      return new Promise((resolve, reject) => {
        axios
          .post('RaspApp/SingleBanked/SaveMessage', temp)
          .then((response) => {
            const { data } = response;
            resolve(data.Message);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    resetInformation({ commit }) {
      commit('RESET_INFORMATION');
    },
  },
  getters: {

  },
};

export default pastTest;
