import axios from 'axios';
import { Module } from 'vuex';
import { IActivityItem } from '@/types/interface';
import { parseString } from 'xml2js';
import {
  ITaskOrigin, RootState, TaskState,
} from './types';

const task: Module<TaskState, RootState> = {
  namespaced: true,
  state: {
    tasks: [],
  },
  mutations: {
    SAVE_TASKS(state, data) {
      state.tasks = data.map((d: ITaskOrigin) => ({
        id: d.guid,
        title: d.task_title,
        description: d.description,
        start: d.action_date_s,
        end: d.action_date_e,
        planStart: d.publish_date_s,
        planEnd: d.publish_date_e,
        imgSmall: d.imgfile_s,
        imgBig: d.imgfile_b,
      }));
    },
  },
  actions: {
    getTaskCenterList({ commit }) {
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/TaskCenter/GetTaskCenter_List')
          .then((response) => {
            const { data } = response;
            commit('SAVE_TASKS', data.Data);
            resolve(data);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    getMemberBalance({ commit, rootState }) {
      // eslint-disable-next-line
      const xml = `<soap12:Envelope xmlns:xsi="http://www.w3.org/2001/XMLSchema-instance" xmlns:xsd="http://www.w3.org/2001/XMLSchema" xmlns:soap12="http://www.w3.org/2003/05/soap-envelope">
                    <soap12:Body>
                      <GetMemBalance xmlns="http://point.public.com.tw/">
                        <account_id>${rootState.memberId}</account_id>
                        <type_id>RP</type_id>
                        <vendor>4010</vendor>
                        <group_id>4</group_id>
                      </GetMemBalance>
                    </soap12:Body>
                   </soap12:Envelope>`;
      return new Promise((resolve, reject) => {
        axios
          .post('PointWebService/point.asmx?op=GetMemBalance',
            xml,
            {
              headers: { 'Content-Type': 'text/xml' },
            })
          .then((response) => {
            const { data } = response;
            parseString(data, (err: any, result: any) => {
              // const temp = result['soap:Envelope']['soap:Body'][0].GetMemberCouponListResponse[0].GetMemberCouponListResult[0];
              // commit('SAVE_MEMBER_COUPON_LIST', JSON.parse(JSON.parse(temp)));
              console.log(data);
            });
            resolve(data);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    getMemberPayDetail({ commit, rootState }) {
      // eslint-disable-next-line
      const xml = `<soap12:Envelope xmlns:xsi="http://www.w3.org/2001/XMLSchema-instance" xmlns:xsd="http://www.w3.org/2001/XMLSchema" xmlns:soap12="http://www.w3.org/2003/05/soap-envelope">
                    <soap12:Body>
                      <GetPayDetail xmlns="http://point.public.com.tw/">
                        <account_id>${rootState.memberId}</account_id>
                        <type_id>RP</type_id>
                        <vendor>4010</vendor>
                        <group_id>4</group_id>
                      </GetPayDetail>
                    </soap12:Body>
                   </soap12:Envelope>`;
      return new Promise((resolve, reject) => {
        axios
          .post('PointWebService/point.asmx?op=GetPayDetail',
            xml,
            {
              headers: { 'Content-Type': 'text/xml' },
            })
          .then((response) => {
            const { data } = response;
            parseString(data, (err: any, result: any) => {
              // const temp = result['soap:Envelope']['soap:Body'][0].GetMemberCouponListResponse[0].GetMemberCouponListResult[0];
              // commit('SAVE_MEMBER_COUPON_LIST', JSON.parse(JSON.parse(temp)));
              console.log(data);
            });
            resolve(data);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
  },
  getters: {
    taskById: (state) => (id: string): IActivityItem | undefined => state.tasks.find((d: IActivityItem) => d.id === id),
  },
};

export default task;
