import { Module } from 'vuex';
import { ArticleState, RootState, IArticleOrigin } from '@/store/types';
import axios from 'axios';
import { IArticle } from '@/types/interface';

const article: Module<ArticleState, RootState> = {
  namespaced: true,
  state: {
    articleList: [],
  },
  mutations: {
    SAVE_ARTICLE_LIST(state, list) {
      state.articleList = list.map((d: IArticleOrigin) => {
        const temp = d.itemvalue.split('#');
        temp.splice(0, 1);
        return {
          id: d.rID,
          title: d.title,
          imgUrl: d.imageUrl,
          subTitle: d.create_date,
          brief: d.seoDescription,
          hashtags: temp,
          url: d.url,
        };
      });
    },
    SAVE_ARTICLE_SIMPLE(state, item) {
      const idx = state.articleList.findIndex((d: IArticle) => d.id === item.id);
      if (idx >= 0) state.articleList[idx].note = item.data.note;
    },
  },
  actions: {
    getArticleList({ commit, rootState }, params) {
      const temp = {
        ...params,
        UserGuid: rootState.userId,
      };
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/Article/GetArticle_List', temp)
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') commit('SAVE_ARTICLE_LIST', data.Data);
            resolve();
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    getArticleSimple({ commit, rootState }, id) {
      const temp = {
        rID: id,
      };
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/Article/GetArticle_Simple', temp)
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') commit('SAVE_ARTICLE_SIMPLE', { id, data: data.Data[0] });
            resolve();
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
  },
  getters: {
    articleById: (state) => (id: number): IArticle | undefined => state.articleList.find((d: IArticle) => d.id === id),
  },
};

export default article;
