import axios from 'axios';
import { Module } from 'vuex';
import moment from 'moment';
import { IListItem, IQuestionItem } from '@/types/interface';
import {
  RootState, OnlineTestState, ISubjectCourseOrigin, IAmountListOrigin, IScopeListOrigin, IQuestionOrigin, IQuestionRecordOrigin,
} from './types';

const mapIntToChar = ['-', 'A', 'B', 'C', 'D', 'E', 'F'];

function mockQuzType(type: string) {
  return type === '1' ? '' : '';
}

function appendSrcAndMiddle(ori: string) {
  const temp = ori.replaceAll('src="', 'src="http://elite.public.com.tw');
  return temp.indexOf('style') < 0
    ? temp.replaceAll('>', 'style="vertical-align: middle;">')
    : temp.replaceAll('style="', 'style="vertical-align: middle; ');
}

const onlineTest: Module<OnlineTestState, RootState> = {
  namespaced: true,
  state: {
    subjectCourseList: [],
    amountList: [],
    scopeList: [],
    amount: '',
    course: '',
    subject: '',
    type: '',
    level: '',
    year: '',
    keyword: '',
    questionList: [],
    testId: '',
    recordList: [],
  },
  mutations: {
    SAVE_SUBJECT_COURSE_LIST(state, list) {
      state.subjectCourseList = list.map((d: ISubjectCourseOrigin) => ({
        id: d.rid,
        title: d.Title,
      }));
    },
    SAVE_AMOUNT_LIST(state, list) {
      state.amountList = list.map((d: IAmountListOrigin) => ({
        id: d.value,
        title: d.name,
      }));
    },
    SAVE_SCOPE_LIST(state, list) {
      state.scopeList = list.map((d: IScopeListOrigin) => ({
        id: d.value,
        title: d.name,
      }));
    },
    SAVE_CRITERIA_AMOUNT(state, amount) {
      if (amount) state.amount = amount;
    },
    SAVE_CRITERIA_COURSE(state, course) {
      if (course) state.course = course;
    },
    SAVE_CRITERIA_SUBJECT(state, subject) {
      if (subject) state.subject = subject;
    },
    SAVE_CRITERIA_YEAR(state, year) {
      if (year) state.year = year;
    },
    SAVE_CRITERIA_KEYWORD(state, keyword) {
      if (keyword) state.keyword = keyword;
    },
    SAVE_CRITERIA_TYPE(state, type) {
      if (type) state.type = type;
    },
    SAVE_CRITERIA_LEVEL(state, level) {
      if (level) state.level = level;
    },
    SAVE_QUESTION_LIST(state, data) {
      state.questionList = data.map((d: IQuestionOrigin) => {
        const ansTemp = [];
        for (let i = 1; i <= d.AnsNum; i += 1) {
          ansTemp.push({
            id: mapIntToChar[i],
            title: appendSrcAndMiddle(d[`Ans${i}`]),
          });
        }

        const temp: IQuestionItem = {
          qId: d.question_id,
          qNum: d.sort1,
          question: `<span>${d.sort1 + 1}.</span>${appendSrcAndMiddle(d.qt)}<span>-${mockQuzType(d.ans_type)}</span>`,
          qType: d.ans_type,
          answer: ansTemp,
          right: [d.AnsR],
        };
        return temp;
      });
    },
    SAVE_TEST_ID(state, id) {
      state.testId = id;
    },
    SAVE_QUESTION_RECORD(state, list) {
      state.recordList = list.map((item: IQuestionRecordOrigin) => ({
        testId: item.test_id,
        totalSec: item.total_sec,
        yearName: item.year_name,
        amountName: item.amount_name,
        typeName: item.type_name === null ? undefined : item.type_name,
        levelName: item.level_name === null ? undefined : item.level_name,
        courseName: item.course_name === null ? undefined : item.course_name,
        className: item.class_name,
        testKeyword: item.test_keyword === null ? undefined : item.test_keyword,
        accountId: item.account_id,
        testRight: item.test_right,
        testMis: item.test_mis,
        testDateBegin: item.test_date_begin,
        testDateEnd: item.test_date_end,
        isFin: item.isfin,
        createDate: moment(item.create_date).format('YYYY/MM/DD a HH:mm:ss'),
      }));
    },
  },
  actions: {
    getSubjectCourseList({ commit }) {
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/OnlineTest/Get_SubjectCourse_List')
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') commit('SAVE_SUBJECT_COURSE_LIST', data.Data);
            resolve(data.Data);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    getAmountList({ commit }) {
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/OnlineTest/Get_Amount_List')
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') commit('SAVE_AMOUNT_LIST', data.Data);
            resolve(data.Data);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    getScopeList({ commit }) {
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/OnlineTest/Get_Scope_List')
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') commit('SAVE_SCOPE_LIST', data.Data);
            resolve(data.Data);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    postCheckAmount({ commit, rootState }, params) {
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/OnlineTest/Post_Check_Amount', params)
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000' && data.Data) {
              commit('SAVE_CRITERIA_AMOUNT', params.amount);
              commit('SAVE_CRITERIA_COURSE', params.course);
              commit('SAVE_CRITERIA_SUBJECT', params.subject);
              commit('SAVE_CRITERIA_TYPE', params.type);
              commit('SAVE_CRITERIA_LEVEL', params.level);
              commit('SAVE_CRITERIA_YEAR', params.year);
              commit('SAVE_CRITERIA_KEYWORD', params.keyword);
              resolve();
            } else reject();
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    postCreateTestList({ commit, rootState }, params) {
      const temp = {
        ...params,
        // eslint-disable-next-line
        account_id: rootState.memberId,
      };
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/OnlineTest/Post_Create_Test_List', temp)
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000' && data.Data[0].data.length !== 0) {
              commit('SAVE_QUESTION_LIST', data.Data[0].data);
              commit('SAVE_TEST_ID', data.Data[0].test_id);
            }
            resolve(data.Data);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    postCalcTest({ commit, state }, params) {
      const temp = {
        // eslint-disable-next-line
        test_id: state.testId,
        // eslint-disable-next-line
        fin_time: params.time,
        // eslint-disable-next-line
        test_content: params.content,
      };
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/OnlineTest/Post_Calc_Test', temp)
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') resolve();
            else reject();
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    postReturnTest({ commit, state }) {
      const temp = {
        // eslint-disable-next-line
        test_id: state.testId,
      };
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/OnlineTest/Post_Return_Test', temp)
          .then((response) => {
            const { data } = response;
            console.log(data.Data);
            resolve();
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    getMemberTestList({ commit, rootState }) {
      const temp = {
        // eslint-disable-next-line
        member_id: rootState.memberId,
      };

      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/OnlineTest/Post_Member_List', temp)
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') {
              commit('SAVE_QUESTION_RECORD', data.Data);
              resolve();
            } else reject();
            console.log(data.Data);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
  },
  getters: {
    courseName: (state) => {
      const temp = state.subjectCourseList.find((d: IListItem) => d.id === state.course);
      if (temp) return temp.title;
      return '';
    },
    amountName: (state) => {
      const temp = state.amountList.find((d: IListItem) => d.id === state.amount);
      if (temp) return temp.title;
      return '';
    },
    yearName: (state) => {
      const temp = state.scopeList.find((d: IListItem) => d.id === state.year);
      if (temp) return temp.title;
      return '';
    },
    typeName: (state, getters, rootState, rootGetters) => {
      if (!rootGetters.typeList) return '';
      const temp = rootGetters.typeList.find((d: IListItem) => d.id === state.type);
      if (temp) return temp.title;
      return '';
    },
    levelName: (state, getters, rootState, rootGetters) => {
      if (!rootGetters.levelList) return '';
      const temp = rootGetters.levelList.find((d: IListItem) => d.id === state.level);
      if (temp) return temp.title;
      return '';
    },
    subjectName: (state, getters, rootState, rootGetters) => {
      if (!rootGetters.subjectList) return '';
      const temp = rootGetters.subjectList.find((d: IListItem) => d.id === state.subject);
      if (temp) return temp.title;
      return '';
    },
    testInfo: (state, getters) => ({
      course: getters.courseName !== '' ? getters.courseName : undefined,
      amount: getters.amountName !== '' ? getters.amountName : undefined,
      year: getters.yearName !== '' ? getters.yearName : undefined,
      type: getters.typeName !== '' ? getters.typeName : undefined,
      level: getters.levelName !== '' ? getters.levelName : undefined,
      subject: getters.subjectName !== '' ? getters.subjectName : undefined,
      keyword: state.keyword !== '' ? state.keyword : undefined,
    }),
  },
};

export default onlineTest;
