import { Module } from 'vuex';
import { ExamInfoState, RootState, IExamInfoOrigin } from '@/store/types';
import axios from 'axios';
import { IArticle } from '@/types/interface';

const examInfo: Module<ExamInfoState, RootState> = {
  namespaced: true,
  state: {
    examInfoList: [],
    examInfoSimpleList: [],
  },
  mutations: {
    SAVE_EXAM_INFO_LIST(state, list) {
      state.examInfoList = list.map((d: IExamInfoOrigin) => {
        const temp = d.itemvalue.split('#');
        temp.splice(0, 1);

        return {
          id: d.rID,
          title: d.title,
          imgUrl: d.imageUrl,
          subTitle: d.create_date,
          source: d.source,
          brief: d.seoDescription,
          hashtags: temp,
          url: d.url,
          hits: d.hits,
        };
      });
    },
    SAVE_EXAM_INFO_SIMPLE(state, item) {
      const temp = state.examInfoList.find((d: IArticle) => d.id === item.id);
      if (temp) {
        const idx = state.examInfoSimpleList.findIndex((d: IArticle) => d.id === item.id);
        temp.note = item.data.note;
        if (idx < 0) state.examInfoSimpleList.push(temp);
        else state.examInfoSimpleList[idx] = temp;
      }
    },
  },
  actions: {
    getExamInfoList({ commit, rootState }, params) {
      const temp = {
        count: params.isLongPeriod ? '20' : '4',
        days: params.isLongPeriod ? '400' : '400',
        // eslint-disable-next-line @typescript-eslint/camelcase
        search_key: params.searchKey,
        keyword: params.keyword,
        UserGuid: rootState.userId,
      };

      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/ExamInfo/GetExamInfo_List', temp)
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') commit('SAVE_EXAM_INFO_LIST', data.Data);
            if (data.Code === '7997') commit('SAVE_EXAM_INFO_LIST', []);
            resolve();
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    getExamInfoSimple({ commit, rootState }, id) {
      const temp = {
        rID: id,
      };
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/ExamInfo/GetExamInfo_Simple', temp)
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') commit('SAVE_EXAM_INFO_SIMPLE', { id, data: data.Data[0] });
            resolve();
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
  },
  getters: {
    examInfoById: (state) => (id: number): IArticle | undefined => state.examInfoSimpleList.find((d: IArticle) => d.id === id),
  },
};

export default examInfo;
