import { Module } from 'vuex';
import { ITicket } from '@/types/interface';
import axios from 'axios';
import moment from 'moment';
import { parseString } from 'xml2js';
import { RootState, TicketState, ICouponOrigin } from './types';

const ticket: Module<TicketState, RootState> = {
  namespaced: true,
  state: {
    ticketList: [],
    ticketDetail: {
      content: '',
      image: '',
    },
  },
  mutations: {
    SAVE_MEMBER_COUPON_LIST(state, list) {
      state.ticketList = list.map((d: ICouponOrigin) => {
        let status = '';
        if (d.save_date_string !== null) status = 'used';
        else {
          status = d.date_flag === '' ? 'expired' : 'available';
        }

        return {
          id: d.unique_code,
          status,
          title: d.act_name,
          start: moment(d.valid_from_date_string).format('YYYY-MM-DD'),
          end: moment(d.valid_to_date_string).format('YYYY-MM-DD'),
          code: d.unique_code,
          linkId: d.ex_link_id,
        };
      });
    },
    SAVE_TICKET_DETAIL(state, data) {
      state.ticketDetail = data;
    },
  },
  actions: {
    getMemberCouponList({ commit, rootState }) {
      // eslint-disable-next-line
      const xml = `<soap12:Envelope xmlns:xsi="http://www.w3.org/2001/XMLSchema-instance" xmlns:xsd="http://www.w3.org/2001/XMLSchema" xmlns:soap12="http://www.w3.org/2003/05/soap-envelope">
                    <soap12:Body>
                      <GetMemberCouponList xmlns="http://point.public.com.tw/">
                        <account_id>${rootState.memberId}</account_id>
                        <status></status>
                      </GetMemberCouponList>
                    </soap12:Body>
                   </soap12:Envelope>`;
      return new Promise((resolve, reject) => {
        axios
          .post('PointWebService/point.asmx?op=GetMemberCouponList',
            xml,
            {
              headers: { 'Content-Type': 'text/xml' },
            })
          .then((response) => {
            const { data } = response;
            parseString(data, (err: any, result: any) => {
              const temp = result['soap:Envelope']['soap:Body'][0].GetMemberCouponListResponse[0].GetMemberCouponListResult[0];
              commit('SAVE_MEMBER_COUPON_LIST', JSON.parse(JSON.parse(temp)));
            });
            resolve(data);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    getTicket({ commit }, id: string) {
      const temp = {
        // eslint-disable-next-line
        ticket_rownum: id,
      };
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/ADTicket/GetTicket_Simple', temp)
          .then((response) => {
            const { data } = response;
            commit('SAVE_TICKET_DETAIL', data.Data);
            resolve(data);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
  },
  getters: {
    ticketById: (state) => (id: string): ITicket | undefined => state.ticketList.find((d: ITicket) => d.id === id),
    detailContent: (state) => state.ticketDetail.content,
    detailImage: (state) => state.ticketDetail.image,
  },
};

export default ticket;
