import axios from 'axios';
import { Module } from 'vuex';
import { IListItem, ITopicItem } from '@/types/interface';
import { RootState, SubscriptionState, ITopicOrigin } from './types';

const subscription: Module<SubscriptionState, RootState> = {
  namespaced: true,
  state: {
    topicList: [],
    userSubList: [],
  },
  mutations: {
    SAVE_TOPIC_LIST(state, list) {
      const temp: ITopicItem[] = [];
      list.forEach((d: ITopicOrigin) => {
        if (d.topic !== null) {
          temp.push({
            types: {
              id: d.types.split(':')[1],
              title: d.types.split(':')[0],
            },
            topic: d.topic.split('').map((t: string) => ({
              id: t.split(':')[1],
              title: t.split(':')[0],
            })),
          });
        }
      });
      state.topicList = temp;
    },
    SAVE_USER_SUB(state, list) {
      state.userSubList = list.map((d: any) => d.topicId);
    },
  },
  actions: {
    getTopic({ commit }) {
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/GetTopic',
            {
              serid: '4010',
              security: 'A3B0B958-DC13-4827-B663-3E4C130F28D6',
            })
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') commit('SAVE_TOPIC_LIST', data.Data);
            resolve(data.Data);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    postUserSub({ rootState }, list: string[]) {
      const temp = {
        UserGuid: rootState.userId,
        topicId: list.join(','),
        serid: '4010',
      };
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/PostUserSub', temp)
          .then((response) => {
            const { data } = response;
            resolve(data);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    getUserSub({ commit, rootState }) {
      const temp = {
        UserGuid: rootState.userId,
        serid: '4010',
      };
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/GetUserSub', temp)
          .then((response) => {
            const { data } = response;
            commit('SAVE_USER_SUB', data.Data);
            resolve(data);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
  },
  getters: {
    userSubTopicList: (state) => {
      const result: IListItem[] = [];
      state.userSubList.forEach((id: string) => {
        const list = state.topicList.find((d: ITopicItem) => JSON.stringify(d.topic).indexOf(id) >= 0);
        if (list) {
          const temp = list.topic.find((d: IListItem) => d.id === id);
          if (temp) result.push(temp);
        }
      });

      return result;
    },
    filterTopicList: (state, getters) => {
      const result: IListItem[] = [];
      state.topicList.forEach((item: ITopicItem) => {
        item.topic.forEach((d: IListItem) => {
          const temp = getters.userSubTopicList.find((t: IListItem) => t.id === d.id);
          if (!temp) result.push(d);
        });
      });

      return result;
    },
    allTopicList: (state) => {
      const result: IListItem[] = [];
      state.topicList.forEach((item: ITopicItem) => {
        item.topic.forEach((d: IListItem) => {
          result.push(d);
        });
      });

      return result;
    },
  },
};

export default subscription;
